import './App.css';
import About from './Components/About'
import Home from './Components/Home'
import {BrowserRouter,Routes,Route,Link}from 'react-router-dom';
import main from './main.png';
import Profile from "./Components/Profile/Profile";
import Login from "./Components/Login/Login";
import Register from "./Components/Register/Register";
import { useState } from "react"; 
import ChatBot from "react-simple-chatbot";
import {Segment} from "semantic-ui-react";

function App() {
  const [userstate, setUserState] = useState({});
  const steps=[
    {
      id:'Greet',
      message:'Hello,Welcome to our Website',
      trigger:'Ask Name'
    },
    {
      id:'Ask Name',
      message:'Please enter your name',
      trigger:'waiting1'
    },
    {
      id:'waiting1',
      user:true,
      trigger:'Name'
    },
    {
      id:'Name',
      message:'Hi {previousValue} I a Emo, Please tell your symptoms',
      trigger:'issues'
    },
    {
      id:'issues',
      options:[
        {value:'happy',label:'happy',trigger:'happy'},
        {value:'sad',label:'sad',trigger:'sad'},
      ]
    },{
      id:'happy',
      message:'Glad to learn that you are happy',
      end:true,
    },{
      id:'sad',
      message:'You may have depression',
      end:true,
    },
  ];
  return (
    <>
    <BrowserRouter>
            <nav className="navbar navbar-expand-lg " data-bs-theme="dark" style={{backgroundColor:"#000a1a"}}>
                <Link className="navbar-brand mx-4" to="/">
                    <img src={main} alt="Logo" width="120" height="50" className="d-inline-block align-text-top"/>
                </Link>
                <Link className="navbar-brand " to="/">Home</Link>
                
                <Link className="navbar-brand mx-3" to="/About">AboutUs</Link>
                <div className="topnav-right">
                <Link className='navbar-brand ' style={{float:"right"}} to='/Login'> <button className="btn btn-outline-success my-9" type="submit" style={{ marginLeft: "900px" ,marginRight:"8px",padding: "8px 32px",fontSize:"24px",float:"right"}}>Login</button></Link>
                </div>
                </nav>

            <Routes>
              <Route path="/Login" element={<Login/>}></Route>
                <Route path="/About" element={<About />}></Route>
                <Route path="/" element={<Home />}></Route>
                  <Route
                    path="/"
                    element={
                      userstate && userstate._id ? (
                        <Profile
                          setUserState={setUserState}
                          username={userstate.fname}
                        />
                      ) : (
                        <Login setUserState={setUserState} />
                      )
                    }
                  ></Route>
                  <Route
                    path="/login"
                    element={<Login setUserState={setUserState} />}
                  ></Route>
                  <Route path="/signup" element={<Register />}></Route>
                </Routes>
            
        </BrowserRouter>
        <Segment floated="right">
          <ChatBot steps={steps}/>
        </Segment>

    </>
  );
}

export default App;
