import React from 'react'
import './Home.css'
import Typewriter from 'typewriter-effect';

function Home() {
  return (
    <>
     <div className='imageFor mx-2 my-2'> 
    <h1 className="writer">
    <Typewriter
      options={{
        strings: ['EmoCare Hub', 'Welcomes You','Ready to help You'],
        autoStart: true,
        loop: true,
      }}
    />
    </h1></div>
    
    <div className="container">
    <h2 className="mx-2 my-4"> <u style={{paddingBottom:"5px",
    textDecoration:"none",
    borderBottom:"3px double #000"}}>Introduction</u> :-</h2>
    <p style={{fontSize:"18px"}}>Depression is a disorder of major public health importance, in terms of its prevalence and the suffering, dysfunction, morbidity, and economic burden. Depression is more common in women than men. The report on Global Burden of Disease estimates the point prevalence of unipolar depressive episodes to be 1.9% for men and 3.2% for women, and the one-year prevalence has been estimated to be 5.8% for men and 9.5% for women. It is estimated that by the year 2020 if current trends for demographic and epidemiological transition continue, the burden of depression will increase to 5.7% of the total burden of disease and it would be the second leading cause of disability-adjusted life years (DALYs), second only to ischemic heart disease.
      In view of the morbidity, depression as a disorder has always been a focus of attention of researchers in India. Various authors have tried to study its prevalence, nosological issues, psychosocial risk factors including life events, sympto matology in the cultural context, comorbidity, psychoneurobiology, treatment, outcome, prevention, disability and burden. Some of the studies have also tried to address various issues in children and elderly.
      </p>
      <h2 className="mx-2 my-4"> <u style={{paddingBottom:"5px",
    textDecoration:"none",
    borderBottom:"3px double #000"}}>Epidemiology</u> :-</h2>
      <p style={{fontSize:"18px"}}>Many studies have estimated the prevalence of depression in community samples and the prevalence rates have varied from 1.7 to 74 per thousand population. Reddy and Chandrasekhar carried out a metanalysis, which included 13 studies on epidemiology of psychiatric disorders which include 33572 subjects from the community and reported prevalence of depression to be 7.9 to 8.9 per thousand population and the prevalence rates were nearly twice in the urban areas. The findings with regard to prevalence in urban population are in line with the findings of a survey done on the entire adult population of an industrial township, which showed that the prevalence rate for depression to be 19.4 per thousand.</p>
      <h2 className="mx-2 my-4"> <u style={{paddingBottom:"5px",
    textDecoration:"none",
    borderBottom:"3px double #000"}}>Impact of Depression</u> :-</h2>
    <p style={{fontSize:"18px"}}>Studies have shown that depressive disorders lead to significant dysfunction, disability and poor quality of life in sufferers and pose a significant burden on the caregivers. The pattern of burden experienced by relatives of patients with affective disorders and schizophrenia have been shown to be similar, being principally felt in the areas of family routine, leisure, interaction and finances. However, the caregivers of subjects with depression experience lesser degree of burden compared to caregivers of schizophrenia and bipolar disorders. Another study showed that the burden of dysthymia is similar to neurotic disorders like obsessive compulsive disorder and generalized anxiety disorder. It has also been seen that patients with dysthymia have significant impairment on measures of quality of life, disability, social support and marital adjustment compared to normal/medically ill controls. The study also showed that duration of illness and severity of depression are the most important correlates of impaired quality of life and disability. A study assessing the relationship of stigma to both depression and somatization in psychiatric patients of south India showed that although both depressive and somatic symptoms were distressing, perceived stigma was more for depressive symptoms. Depressive symptoms were perceived as socially disadvantageous as compared to somatization symptoms.
      </p> 
      <marquee style={{fontSize:"16px"}}><a href="https://www.ncbi.nlm.nih.gov/pmc/articles/PMC3146226/">For more details Click!</a></marquee>
       
    </div>
    </>
  )
}

export default Home